'use client'

import { useState, useEffect } from 'react'
import { useUser } from '@/context/UserContext'
import { useRouter } from 'next/navigation'
import { Button } from '@/components/ui/button'
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card'
import { Tabs, TabsContent, TabsList, TabsTrigger } from '@/components/ui/tabs'
import { Input } from '@/components/ui/input'
import { Label } from '@/components/ui/label'
import { Textarea } from '@/components/ui/textarea'
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from '@/components/ui/select'
import { Table, TableBody, TableCell, TableHead, TableHeader, TableRow } from '@/components/ui/table'
import { Badge } from '@/components/ui/badge'
import { Alert, AlertDescription } from '@/components/ui/alert'
import { Dialog, DialogContent, DialogDescription, DialogHeader, DialogTitle, DialogTrigger } from '@/components/ui/dialog'

interface Product {
  id: string
  title: string
  description: string
  price: number
  rentalPrice?: number
  category: string
  condition: string
  availability: 'available' | 'sold' | 'rented'
  dateAdded: string
  views: number
}

interface Order {
  id: string
  buyer: string
  item: string
  type: 'purchase' | 'rental'
  amount: string
  status: 'pending' | 'confirmed' | 'shipped' | 'delivered' | 'completed'
  date: string
  rentalPeriod?: string
}

export default function SellerDashboard() {
  const { user, isAuthenticated } = useUser()
  const router = useRouter()
  const [products, setProducts] = useState<Product[]>([])
  const [orders, setOrders] = useState<Order[]>([])
  const [isAddingProduct, setIsAddingProduct] = useState(false)
  const [newProduct, setNewProduct] = useState({
    title: '',
    description: '',
    price: '',
    rentalPrice: '',
    category: '',
    condition: ''
  })

  useEffect(() => {
    if (!isAuthenticated || user?.role !== 'seller') {
      router.push('/login')
      return
    }

    // Load demo data
    setProducts([
      {
        id: '1',
        title: 'Vintage Leather Jacket',
        description: 'Classic brown leather jacket in excellent condition',
        price: 45,
        rentalPrice: 8,
        category: 'Fashion',
        condition: 'Good',
        availability: 'available',
        dateAdded: '2024-02-15',
        views: 23
      },
      {
        id: '2',
        title: 'Canon DSLR Camera',
        description: 'Professional camera with lens kit',
        price: 350,
        rentalPrice: 25,
        category: 'Electronics',
        condition: 'Excellent',
        availability: 'rented',
        dateAdded: '2024-02-20',
        views: 45
      },
      {
        id: '3',
        title: 'Wooden Coffee Table',
        description: 'Handcrafted oak coffee table',
        price: 120,
        category: 'Furniture',
        condition: 'Very Good',
        availability: 'sold',
        dateAdded: '2024-02-10',
        views: 12
      }
    ])

    setOrders([
      {
        id: '1',
        buyer: 'Jane Buyer',
        item: 'Canon DSLR Camera',
        type: 'rental',
        amount: '$75 (3 days)',
        status: 'confirmed',
        date: '2024-03-01',
        rentalPeriod: '3 days'
      },
      {
        id: '2',
        buyer: 'Mike Trader',
        item: 'Wooden Coffee Table',
        type: 'purchase',
        amount: '$120',
        status: 'delivered',
        date: '2024-02-28'
      }
    ])
  }, [isAuthenticated, user, router])

  const handleAddProduct = () => {
    if (!newProduct.title || !newProduct.price || !newProduct.category) {
      return
    }

    const product: Product = {
      id: Date.now().toString(),
      title: newProduct.title,
      description: newProduct.description,
      price: parseFloat(newProduct.price),
      rentalPrice: newProduct.rentalPrice ? parseFloat(newProduct.rentalPrice) : undefined,
      category: newProduct.category,
      condition: newProduct.condition,
      availability: 'available',
      dateAdded: new Date().toISOString().split('T')[0],
      views: 0
    }

    setProducts([...products, product])
    setNewProduct({
      title: '',
      description: '',
      price: '',
      rentalPrice: '',
      category: '',
      condition: ''
    })
    setIsAddingProduct(false)
  }

  const handleOrderStatusUpdate = (orderId: string, newStatus: string) => {
    setOrders(orders.map(order => 
      order.id === orderId 
        ? { ...order, status: newStatus as Order['status'] }
        : order
    ))
  }

  if (!isAuthenticated || user?.role !== 'seller') {
    return (
      <Alert>
        <AlertDescription>
          Access denied. Seller privileges required.
        </AlertDescription>
      </Alert>
    )
  }

  const stats = {
    totalProducts: products.length,
    availableProducts: products.filter(p => p.availability === 'available').length,
    totalOrders: orders.length,
    pendingOrders: orders.filter(o => o.status === 'pending' || o.status === 'confirmed').length
  }

  return (
    <div className="space-y-8">
      <div className="flex justify-between items-center">
        <div>
          <h1 className="text-3xl font-bold text-gray-900">Seller Dashboard</h1>
          <p className="text-gray-600 mt-2">Manage your inventory and orders</p>
        </div>
        <Dialog open={isAddingProduct} onOpenChange={setIsAddingProduct}>
          <DialogTrigger asChild>
            <Button>Add New Item</Button>
          </DialogTrigger>
          <DialogContent className="max-w-md">
            <DialogHeader>
              <DialogTitle>Add New Item</DialogTitle>
              <DialogDescription>
                List a new item for sale or rent
              </DialogDescription>
            </DialogHeader>
            <div className="space-y-4">
              <div className="space-y-2">
                <Label htmlFor="title">Item Title</Label>
                <Input
                  id="title"
                  placeholder="Enter item title"
                  value={newProduct.title}
                  onChange={(e) => setNewProduct({...newProduct, title: e.target.value})}
                />
              </div>
              
              <div className="space-y-2">
                <Label htmlFor="description">Description</Label>
                <Textarea
                  id="description"
                  placeholder="Describe your item"
                  value={newProduct.description}
                  onChange={(e) => setNewProduct({...newProduct, description: e.target.value})}
                />
              </div>
              
              <div className="grid grid-cols-2 gap-4">
                <div className="space-y-2">
                  <Label htmlFor="price">Sale Price ($)</Label>
                  <Input
                    id="price"
                    type="number"
                    placeholder="0.00"
                    value={newProduct.price}
                    onChange={(e) => setNewProduct({...newProduct, price: e.target.value})}
                  />
                </div>
                
                <div className="space-y-2">
                  <Label htmlFor="rentalPrice">Rental Price/Day ($)</Label>
                  <Input
                    id="rentalPrice"
                    type="number"
                    placeholder="0.00 (optional)"
                    value={newProduct.rentalPrice}
                    onChange={(e) => setNewProduct({...newProduct, rentalPrice: e.target.value})}
                  />
                </div>
              </div>
              
              <div className="space-y-2">
                <Label htmlFor="category">Category</Label>
                <Select value={newProduct.category} onValueChange={(value) => setNewProduct({...newProduct, category: value})}>
                  <SelectTrigger>
                    <SelectValue placeholder="Select category" />
                  </SelectTrigger>
                  <SelectContent>
                    <SelectItem value="Electronics">Electronics</SelectItem>
                    <SelectItem value="Fashion">Fashion</SelectItem>
                    <SelectItem value="Furniture">Furniture</SelectItem>
                    <SelectItem value="Books">Books</SelectItem>
                    <SelectItem value="Sports">Sports</SelectItem>
                    <SelectItem value="Other">Other</SelectItem>
                  </SelectContent>
                </Select>
              </div>
              
              <div className="space-y-2">
                <Label htmlFor="condition">Condition</Label>
                <Select value={newProduct.condition} onValueChange={(value) => setNewProduct({...newProduct, condition: value})}>
                  <SelectTrigger>
                    <SelectValue placeholder="Select condition" />
                  </SelectTrigger>
                  <SelectContent>
                    <SelectItem value="Like New">Like New</SelectItem>
                    <SelectItem value="Excellent">Excellent</SelectItem>
                    <SelectItem value="Very Good">Very Good</SelectItem>
                    <SelectItem value="Good">Good</SelectItem>
                    <SelectItem value="Fair">Fair</SelectItem>
                  </SelectContent>
                </Select>
              </div>
              
              <div className="flex justify-end space-x-2">
                <Button variant="outline" onClick={() => setIsAddingProduct(false)}>
                  Cancel
                </Button>
                <Button onClick={handleAddProduct}>
                  Add Item
                </Button>
              </div>
            </div>
          </DialogContent>
        </Dialog>
      </div>

      {/* Stats Overview */}
      <div className="grid grid-cols-1 md:grid-cols-4 gap-6">
        <Card>
          <CardHeader className="pb-2">
            <CardTitle className="text-sm font-medium text-gray-600">Total Items</CardTitle>
          </CardHeader>
          <CardContent>
            <div className="text-2xl font-bold">{stats.totalProducts}</div>
          </CardContent>
        </Card>
        <Card>
          <CardHeader className="pb-2">
            <CardTitle className="text-sm font-medium text-gray-600">Available Items</CardTitle>
          </CardHeader>
          <CardContent>
            <div className="text-2xl font-bold text-green-600">{stats.availableProducts}</div>
          </CardContent>
        </Card>
        <Card>
          <CardHeader className="pb-2">
            <CardTitle className="text-sm font-medium text-gray-600">Total Orders</CardTitle>
          </CardHeader>
          <CardContent>
            <div className="text-2xl font-bold">{stats.totalOrders}</div>
          </CardContent>
        </Card>
        <Card>
          <CardHeader className="pb-2">
            <CardTitle className="text-sm font-medium text-gray-600">Pending Orders</CardTitle>
          </CardHeader>
          <CardContent>
            <div className="text-2xl font-bold text-orange-600">{stats.pendingOrders}</div>
          </CardContent>
        </Card>
      </div>

      {/* Main Content Tabs */}
      <Tabs defaultValue="inventory" className="space-y-6">
        <TabsList>
          <TabsTrigger value="inventory">My Inventory</TabsTrigger>
          <TabsTrigger value="orders">Orders</TabsTrigger>
          <TabsTrigger value="profile">Store Profile</TabsTrigger>
        </TabsList>

        <TabsContent value="inventory">
          <Card>
            <CardHeader>
              <CardTitle>My Items</CardTitle>
              <CardDescription>Manage your listed items</CardDescription>
            </CardHeader>
            <CardContent>
              <Table>
                <TableHeader>
                  <TableRow>
                    <TableHead>Item</TableHead>
                    <TableHead>Category</TableHead>
                    <TableHead>Sale Price</TableHead>
                    <TableHead>Rental Price</TableHead>
                    <TableHead>Condition</TableHead>
                    <TableHead>Status</TableHead>
                    <TableHead>Views</TableHead>
                    <TableHead>Actions</TableHead>
                  </TableRow>
                </TableHeader>
                <TableBody>
                  {products.map((product) => (
                    <TableRow key={product.id}>
                      <TableCell>
                        <div>
                          <div className="font-medium">{product.title}</div>
                          <div className="text-sm text-gray-500">{product.description}</div>
                        </div>
                      </TableCell>
                      <TableCell>{product.category}</TableCell>
                      <TableCell>${product.price}</TableCell>
                      <TableCell>
                        {product.rentalPrice ? `$${product.rentalPrice}/day` : 'N/A'}
                      </TableCell>
                      <TableCell>{product.condition}</TableCell>
                      <TableCell>
                        <Badge 
                          variant={
                            product.availability === 'available' ? 'default' :
                            product.availability === 'sold' ? 'secondary' : 'outline'
                          }
                        >
                          {product.availability}
                        </Badge>
                      </TableCell>
                      <TableCell>{product.views}</TableCell>
                      <TableCell>
                        <div className="flex space-x-2">
                          <Button size="sm" variant="outline">Edit</Button>
                          <Button size="sm" variant="destructive">Delete</Button>
                        </div>
                      </TableCell>
                    </TableRow>
                  ))}
                </TableBody>
              </Table>
            </CardContent>
          </Card>
        </TabsContent>

        <TabsContent value="orders">
          <Card>
            <CardHeader>
              <CardTitle>Order Management</CardTitle>
              <CardDescription>Track and manage your orders</CardDescription>
            </CardHeader>
            <CardContent>
              <Table>
                <TableHeader>
                  <TableRow>
                    <TableHead>Order ID</TableHead>
                    <TableHead>Buyer</TableHead>
                    <TableHead>Item</TableHead>
                    <TableHead>Type</TableHead>
                    <TableHead>Amount</TableHead>
                    <TableHead>Status</TableHead>
                    <TableHead>Date</TableHead>
                    <TableHead>Actions</TableHead>
                  </TableRow>
                </TableHeader>
                <TableBody>
                  {orders.map((order) => (
                    <TableRow key={order.id}>
                      <TableCell className="font-medium">#{order.id}</TableCell>
                      <TableCell>{order.buyer}</TableCell>
                      <TableCell>{order.item}</TableCell>
                      <TableCell>
                        <Badge variant="outline" className="capitalize">
                          {order.type}
                        </Badge>
                      </TableCell>
                      <TableCell>{order.amount}</TableCell>
                      <TableCell>
                        <Badge 
                          variant={
                            order.status === 'delivered' || order.status === 'completed' ? 'default' :
                            order.status === 'pending' ? 'secondary' : 'outline'
                          }
                        >
                          {order.status}
                        </Badge>
                      </TableCell>
                      <TableCell>{order.date}</TableCell>
                      <TableCell>
                        <Select 
                          value={order.status} 
                          onValueChange={(value) => handleOrderStatusUpdate(order.id, value)}
                        >
                          <SelectTrigger className="w-32">
                            <SelectValue />
                          </SelectTrigger>
                          <SelectContent>
                            <SelectItem value="pending">Pending</SelectItem>
                            <SelectItem value="confirmed">Confirmed</SelectItem>
                            <SelectItem value="shipped">Shipped</SelectItem>
                            <SelectItem value="delivered">Delivered</SelectItem>
                            <SelectItem value="completed">Completed</SelectItem>
                          </SelectContent>
                        </Select>
                      </TableCell>
                    </TableRow>
                  ))}
                </TableBody>
              </Table>
            </CardContent>
          </Card>
        </TabsContent>

        <TabsContent value="profile">
          <Card>
            <CardHeader>
              <CardTitle>Store Profile</CardTitle>
              <CardDescription>Customize your seller profile</CardDescription>
            </CardHeader>
            <CardContent className="space-y-6">
              <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
                <div className="space-y-4">
                  <div className="space-y-2">
                    <Label htmlFor="storeName">Store Name</Label>
                    <Input id="storeName" defaultValue={user?.name + "'s Store"} />
                  </div>
                  
                  <div className="space-y-2">
                    <Label htmlFor="storeDescription">Store Description</Label>
                    <Textarea 
                      id="storeDescription" 
                      placeholder="Tell customers about your store..."
                      defaultValue="Quality pre-loved items at great prices. All items are carefully inspected before listing."
                    />
                  </div>
                  
                  <div className="space-y-2">
                    <Label htmlFor="location">Location</Label>
                    <Input id="location" placeholder="City, State" />
                  </div>
                </div>
                
                <div className="space-y-4">
                  <div className="space-y-2">
                    <Label htmlFor="contactEmail">Contact Email</Label>
                    <Input id="contactEmail" type="email" defaultValue={user?.email} />
                  </div>
                  
                  <div className="space-y-2">
                    <Label htmlFor="phone">Phone Number</Label>
                    <Input id="phone" type="tel" placeholder="(555) 123-4567" />
                  </div>
                  
                  <div className="space-y-2">
                    <Label htmlFor="website">Website (Optional)</Label>
                    <Input id="website" type="url" placeholder="https://yourstore.com" />
                  </div>
                </div>
              </div>
              
              <div className="flex justify-end">
                <Button>Save Profile</Button>
              </div>
            </CardContent>
          </Card>
        </TabsContent>
      </Tabs>
    </div>
  )
}
